.title {
  display: flex;
  margin-bottom: 5px;
  font-size: 14px;
}

.container {
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  margin-inline: 10px;
  align-items: baseline;
  position: relative;
}
@media (max-width: 480px) {
  .container {
    flex-direction: column;
    gap: 10px;
    width: 50%;
    min-width: 150px;
  }
}
