.fieldset {
  border: solid thin lightgray;
  border-radius: 3px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.fieldset > legend {
  font-size: 0.75rem;
  margin: 0 10px;
  padding: 0;
  background-color: transparent;
}
.fieldset > textarea {
  border: none;
  outline: none;
  width: calc(100% - 20px);
  padding: 5px 10px 7.5px 10px;
  font-size: 1rem;
  resize: none;
  background-color: transparent;
}
.fieldset > textarea::placeholder {
  color: transparent;
}
.fieldset > textarea:disabled {
  background-color: transparent;
}
.fieldset > textarea:disabled::-webkit-input-placeholder {
  color: white;
}
.fieldset:has(textarea:disabled) {
  background-color: transparent;
  border: none;
}
.fieldset:has(textarea:disabled) legend::after {
  content: ":";
  padding-left: 1px;
}
