.section {
  flex: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  padding-bottom: 0;
  margin-top: 0;
  gap: 1rem;
  animation: appear 0.3s ease-in-out 0s 1 forwards;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
