.container {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  flex-wrap: wrap;
}
.container > fieldset {
  flex: 1 !important;
}
.container > * {
  flex: 1 !important;
}
@media (max-width: 560px) {
  .container {
    flex-direction: column;
  }
}
