.container {
  padding: 1rem 2rem 1rem 3rem;
  flex: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.container > :nth-child(2) {
  padding-left: 1rem;
}
@media (max-width: 840px) {
  .container {
    padding: 1rem;
  }
  .container > :nth-child(2) {
    padding-left: 0;
  }
}
